.container_Project{
  display: grid;
    grid-template-columns: 1fr 100px;
    grid-template-rows: 40px 40px 40px 1000px;
    grid-template-areas: 
      "main button1"
      "main button2"
      "main button3"
      "main blank";
  width:100%;
  height:1000px;
}
.movie_button{
  grid-area:button1;
 width:30px;
 height:20px;
}
.pet_button{
  grid-area:button2;
  width:30px;
 height:20px;
}
.washer_button{
  grid-area:button3;
  width:30px;
 height:20px;
}
.main_Project{
  grid-area:main;
}