#selfIntro_container{
  display:grid;
  grid-template-columns: 1fr 150px;
  grid-template-rows: 100px 100px 700px ;
  grid-template-areas: 
      "header header"
      "image image"
      "section aside";
}

#border{
  border: 3pt groove black;
}
#selfintro_header{
  grid-area:header;
  font-size:2.5em;
  border-bottom-style:solid;
  border-bottom-width:3px;
  border-bottom-color:gray;
}
#selfintro_section{
  grid-area:section;
  text-indent:30px;
  background-color:#e6dee9;
  border: 1pt groove gray;
  border-radius: 40px;
}
#selfintro_aside{
  grid-area:aside;
}
#selfintro_image{
  grid-area:image;
  padding:20px;
}
.middle_text{
  font-size:2em;
}
