.bookbackground {
    width: 100%;
    height: 100%;
    text-align: center;
    position: relative;
  }
  .bookbackground::after {
    width: 100%;
    height: 700px;
    content: "";
    background: url("../../../public/image/book.jpg");
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    opacity: 0.3;
  }