#bldtext{
    position: relative;
    width:60%;
    height:250px;
    float:left;
}
#bldimg{
    position: relative;
    width:30%;
    height:220px;
    border-radius: 50%;
    float:right;
    overflow: hidden;
    padding-top:30px;
}
#bldimg img{
    position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.volunteerbackground {
    width: 100%;
    height: 100%;
    text-align: center;
    position: relative;
  }
.volunteerbackground::after {
    width: 100%;
    height: 800px;
    content: "";
    background: url("../../../public/image/volunteer_background.jpg");
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    opacity: 0.3;
  }