.licensbackground {
    width: 100%;
    height: 100%;
    text-align: center;
    position: relative;
  }
  .licensbackground::after {
    width: 100%;
    height: 800px;
    content: "";
    background: url("../../../public/image/licens.jpg");
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    opacity: 0.1;
  }